import React, { Suspense, lazy } from 'react';
import ReactDOM from 'react-dom/client';
import { createHashRouter, RouterProvider } from 'react-router-dom';
import './index.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.bundle.min';
import { HelmetProvider } from 'react-helmet-async';

import App from './App';
import ErrorPage from './pages/ErrorPage';

// Lazy-loaded page components
const HomePage = lazy(() => import('./pages/HomePage'));
const ProductsPage = lazy(() => import('./pages/ProductsPage'));
const ProductDetail = lazy(() => import('./pages/ProductDetail'));
const InfoFaqPage = lazy(() => import('./pages/InfoFaqPage'));
const ContactPage = lazy(() => import('./pages/ContactPage'));
const AboutUsPage = lazy(() => import('./pages/AboutUsPage'));
const OpinionPage = lazy(() => import('./pages/OpinionPage'));
const CustomOrderAndStatusPage = lazy(() => import('./pages/CustomOrderAndStatusPage'));
const AdminLoginPage = lazy(() => import('./pages/AdminLoginPage'));
const ThankYouPage = lazy(() => import('./pages/ThankYouPage'));
const AdminDashboardPage = lazy(() => import('./pages/AdminDashboardPage'));

// Use createHashRouter so GH Pages will always load index.html and React handles routing
const router = createHashRouter([
  {
    path: '/',
    element: <App />,
    children: [
      { path: '/', element: <HomePage /> },
      { path: '/products', element: <ProductsPage /> },
      { path: '/products/:id', element: <ProductDetail /> },
      { path: '/info-faq', element: <InfoFaqPage /> },
      { path: '/contact', element: <ContactPage /> },
      { path: '/about-us', element: <AboutUsPage /> },
      { path: '/opinie', element: <OpinionPage /> },
      { path: '/zamowienia', element: <CustomOrderAndStatusPage /> },
      { path: '/podziekowanie', element: <ThankYouPage /> },
      { path: '/admin-login', element: <AdminLoginPage /> },
      {
        path: '/admin-dashboard',
        element: (
          <Suspense fallback={<div>adowanie dashboardu...</div>}>
            <AdminDashboardPage />
          </Suspense>
        ),
      },
    ],
  },
  {
    path: '*',
    element: <ErrorPage />,
  },
]);

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <HelmetProvider>
      <Suspense fallback={<div>adowanie strony...</div>}>
        <RouterProvider router={router} />
      </Suspense>
    </HelmetProvider>
  </React.StrictMode>
);
