import React, { useState, useEffect } from 'react';
import { Collapse } from 'bootstrap';
import { Link, NavLink } from 'react-router-dom';

const Navbar = () => {
    const [scrolled, setScrolled] = useState(false);

    // Effect for navbar background on scroll
    useEffect(() => {
        const handleScroll = () => {
            const isScrolled = window.scrollY > 10;
            if (isScrolled !== scrolled) {
                setScrolled(isScrolled);
            }
        };

        document.addEventListener('scroll', handleScroll);
        return () => {
            document.removeEventListener('scroll', handleScroll);
        };
    }, [scrolled]);

    // Effect for mobile navbar collapse
    useEffect(() => {
        const navbarToggler = document.querySelector('.navbar-toggler');
        const navbarCollapseElement = document.getElementById('navbarNav');

        if (navbarToggler && navbarCollapseElement) {
            // Get the Bootstrap Collapse instance. If it doesn't exist, create one without toggling.
            const bsCollapse = Collapse.getInstance(navbarCollapseElement) || new Collapse(navbarCollapseElement, { toggle: false });

            // Handle toggler button click
            const handleTogglerClick = () => {
                bsCollapse.toggle();
            };
            navbarToggler.addEventListener('click', handleTogglerClick);

            // Handle nav link click
            const handleNavLinkClick = () => {
                if (navbarCollapseElement.classList.contains('show')) {
                    bsCollapse.hide();
                }
            };
            // Attach event listeners to all nav links and the specific "Zamwienia" link
            navbarCollapseElement.querySelectorAll('.nav-link, #zamowienia-nav-link').forEach(link => {
                link.addEventListener('click', handleNavLinkClick);
            });

            // Cleanup function to remove event listeners when component unmounts
            return () => {
                navbarCollapseElement.querySelectorAll('.nav-link, #zamowienia-nav-link').forEach(link => {
                    link.removeEventListener('click', handleNavLinkClick);
                });
            };
        }
    }, []); // Empty dependency array means this effect runs once on mount

    

    return (
        <nav className={`navbar navbar-expand-lg navbar-light fixed-top ${scrolled ? 'scrolled' : ''}`}>
            <div className="container">
                <Link className="navbar-brand fw-bolder fs-4" to="/" aria-label="Strona gwna Rex3D">
                    <img src={`${process.env.PUBLIC_URL}/logo192.png`} alt="Rex3D Logo" style={{ height: '60px', marginRight: '10px' }} />
                    Rex3D
                </Link>
                <button className="navbar-toggler" type="button" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav ms-auto align-items-center">
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => "nav-link" + (isActive ? " active" : "")} to="/">Strona Gwna</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => "nav-link" + (isActive ? " active" : "")} to="/products">Produkty</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => "nav-link" + (isActive ? " active" : "")} to="/about-us">O nas</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => "nav-link" + (isActive ? " active" : "")} to="/info-faq">FAQ</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => "nav-link" + (isActive ? " active" : "")} to="/contact">Kontakt</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className={({ isActive }) => "nav-link" + (isActive ? " active" : "")} to="/opinie">Opinie</NavLink>
                        </li>
                        <li className="nav-item ms-lg-2 mt-2 mt-lg-0">
                            <NavLink className="btn btn-primary btn-cta" to="/zamowienia" id="zamowienia-nav-link">Zamwienia</NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    );
};

export default Navbar;