import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

const Footer = () => {
    const [email, setEmail] = useState('');
    const [status, setStatus] = useState(''); // 'success', 'error', 'sending', ''
    const [configData, setConfigData] = useState(null);
    const [configError, setConfigError] = useState(null);

    useEffect(() => {
        const controller = new AbortController();
        const loadConfig = async () => {
            try {
                const url = `${process.env.PUBLIC_URL || ''}/config.json`;
                const res = await fetch(url, { signal: controller.signal });
                if (!res.ok) throw new Error(`HTTP error! status: ${res.status}`);
                const data = await res.json();
                setConfigData(data);
                setConfigError(null);
            } catch (err) {
                if (err.name === 'AbortError') return;
                console.error('Error fetching config:', err);
                setConfigError(err);
                setConfigData(null);
            }
        };
        loadConfig();
        return () => controller.abort();
    }, []);

    const handleNewsletterSubmit = async (e) => {
        e.preventDefault();
        setStatus('sending');

        if (!configData || !configData.links || !configData.links.newsletterForm) {
            console.error('Newsletter form link not available in config.');
            setStatus('error');
            return;
        }

        // Basic client-side email validation
        const emailTrim = (email || '').trim();
        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(emailTrim)) {
            setStatus('error');
            return;
        }

        try {
            const response = await fetch(configData.links.newsletterForm, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json', 'Accept': 'application/json' },
                body: JSON.stringify({ email: emailTrim }),
            });
            if (response.ok) {
                setStatus('success');
                setEmail('');
            } else {
                // If response returns JSON with errors, log it
                let errBody = null;
                try { errBody = await response.json(); } catch (_) {}
                console.error('Newsletter submission failed', response.status, errBody);
                setStatus('error');
            }
        } catch (error) {
            console.error('Error submitting newsletter form:', error);
            setStatus('error');
        }
    };

    // If config not loaded but we know it's failing, show friendly error so footer isn't stuck forever
    if (!configData && !configError) {
        return (
            <footer>
                <div className="container text-center py-5">
                    <p className="text-muted">adowanie konfiguracji...</p>
                </div>
            </footer>
        );
    }

    if (configError) {
        return (
            <footer>
                <div className="container text-center py-5">
                    <div className="alert alert-warning mb-3">
                        Nie udao si wczyta konfiguracji strony. Sprawd DevTools  Network (config.json).
                    </div>
                    <p className="text-muted small">Jeli to jest deploy na GitHub Pages  upewnij si, e masz `"homepage"` w package.json i e wykonae nowy build.</p>
                </div>
            </footer>
        );
    }

    // Safe access helpers
    const getLink = (key) => (configData && configData.links && configData.links[key]) || '#';

    return (
        <footer>
            <div className="container text-center text-md-start">
                <div className="row">
                    {/* Column 1: Company Info */}
                    <div className="col-md-4 col-lg-4 col-xl-4 mx-auto mb-4">
                        <h6 className="text-uppercase fw-bold">Rex3D</h6>
                        <p className="text-muted">Twoje innowacyjne rozwizania w druku 3D. Od pomysu do precyzyjnego wydruku, realizujemy projekty z pasj.</p>
                        <div className="social-icons mt-4">
                            <a href={getLink('instagram')} className="me-4" aria-label="Odwied nas na Instagramie"><i className="bi bi-instagram"></i></a>
                            <a href={getLink('linkedin')} className="me-4" aria-label="Odwied nas na LinkedIn"><i className="bi bi-linkedin"></i></a>
                            <a href={getLink('vinted')} target="_blank" rel="noopener noreferrer" aria-label="Odwied nas na Vinted"><i className="bi bi-bag"></i></a>
                        </div>
                    </div>

                    {/* Column 2: Quick Links */}
                    <div className="col-md-4 col-lg-2 col-xl-2 mx-auto mb-4">
                        <h6 className="text-uppercase fw-bold">Szybkie Linki</h6>
                        <ul className="list-unstyled">
                            <li className="mb-2"><Link to="/products">Produkty</Link></li>
                            <li className="mb-2"><Link to="/about-us">O nas</Link></li>
                            <li className="mb-2"><Link to="/contact">Kontakt</Link></li>
                            <li className="mb-2"><Link to="/info-faq">FAQ</Link></li>
                        </ul>
                    </div>

                    {/* Column 3: Newsletter */}
                    <div className="col-md-4 col-lg-4 col-xl-4 mx-auto mb-4">
                        <h6 className="text-uppercase fw-bold">Bd na bieco</h6>
                        <p className="text-muted">Zapisz si do newslettera, aby otrzymywa informacje o nowociach i specjalnych ofertach.</p>
                        <form onSubmit={handleNewsletterSubmit} className="mt-4">
                            <div className="input-group">
                                <input
                                    type="email"
                                    className="form-control"
                                    placeholder="Twj email"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    required
                                    style={{ borderRadius: 0 }}
                                />
                                <button className="btn btn-dark" type="submit" disabled={status === 'sending' || !getLink('newsletterForm')}>
                                    {status === 'sending' ? <span className="spinner-border spinner-border-sm"></span> : <i className="bi bi-arrow-right"></i>}
                                </button>
                            </div>
                            {status === 'success' && <p className="text-success mt-2">Dzikujemy za zapisanie si!</p>}
                            {status === 'error' && <p className="text-danger mt-2">Wystpi bd. Sprbuj ponownie.</p>}
                            {!getLink('newsletterForm') && <p className="text-muted mt-2 small">Formularz newslettera chwilowo niedostpny.</p>}
                        </form>
                    </div>
                </div>

                {/* Copyright Section */}
                <div className="text-center mt-4 pt-4 border-top">
                    <p className="mb-0 text-muted">&copy; {new Date().getFullYear()} Rex3D. Wszelkie prawa zastrzeone.</p>
                </div>
            </div>
        </footer>
    );
};

export default Footer;
