import React, { useState } from 'react';
import { useRouteError } from 'react-router-dom';
import '../index.css';

export default function ErrorPage() {
  const error = useRouteError();
  const [isDetailsVisible, setIsDetailsVisible] = useState(false);

  // Log the error for debugging purposes
  console.error("ErrorPage received error:", error);

  const toggleDetails = () => {
    setIsDetailsVisible(!isDetailsVisible);
  };

  // Determine what to display based on the error object
  let errorMessage = "Wystpi nieznany bd.";
  let errorDetails = "Brak szczegowych informacji o bdzie.";
  let errorCode = "N/A";

  if (error) {
    if (error.status) {
      // This is likely an HTTP error from react-router-dom (e.g., 404)
      errorCode = error.status;
      errorMessage = `Bd ${error.status}: ${error.statusText || 'Nieznany'}`;
      errorDetails = error.data || error.message || "Brak dodatkowych danych.";
    } else if (error.message) {
      // This is a JavaScript error
      errorMessage = `Wystpi bd: ${error.message}`;
      errorDetails = error.stack || "Brak stosu wywoa.";
    } else {
      // Generic error object
      errorMessage = "Wystpi nieznany bd.";
      errorDetails = JSON.stringify(error, null, 2);
    }
  } else {
    // No error object provided, likely a 404 caught by path="*"
    errorMessage = "Strona nie znaleziona (404).";
    errorDetails = "Adres, pod ktry prbujesz si dosta, nie istnieje.";
    errorCode = "404";
  }

  return (
    <div id="error-page" className="error-page-container">
      <div className="error-smiley">:(</div>
      <h1>{errorMessage}</h1>
      <p>Przepraszamy za niedogodnoci. Nasz zesp zosta o tym poinformowany.</p>
      <p className="error-code">Kod bdu: {errorCode}</p>
      
      <button onClick={toggleDetails} className="btn btn-outline-light" aria-label="Poka/Ukryj szczegy bdu">
        {isDetailsVisible ? 'Ukryj szczegy techniczne' : 'Poka szczegy techniczne'}
      </button>

      {isDetailsVisible && (
        <div className="error-details">
          <pre>
            <strong>Szczegy:</strong>
            <br />
            {errorDetails}
          </pre>
        </div>
      )}
    </div>
  );
}